@import url('https://fonts.googleapis.com/css2?family=Quicksand&display=swap');

*{
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    font-family: 'Quicksand', sans-serif;
}

.App{
    background-image: url(./assets/background.jpg);
    width: 100vw;
    height: 100vh;
    background-position: center;
}

.title{
    text-align: center;
    padding: 20px 0;
    color: rgb(58, 58, 58);
    font-family: 'Franklin Gothic Medium', 'Arial Narrow', Arial, sans-serif;
}

.input{
    border: none;
    opacity: 0.7;
    font-size: 15px;
    border-bottom-left-radius: 50px;
    border-top-left-radius: 50px;
    padding: 15px 15px;
    width: 250px;
}

.search-btn{
    font-size: 15px;
    padding: 15px 15px;
    border-bottom-right-radius: 50px;
    border-top-right-radius: 50px;
    border: none;
    margin-left: 5px;
    color: rgb(155, 155, 155);
    cursor: pointer;
}

.input:focus, .search-btn:focus {
    border: none;
    outline: none;
}

.container{
    padding-top: 20px;
    text-align: center;
}

.cityName{
    margin-top: 30px;
    color: rgb(51, 51, 51);
    color: antiquewhite;
}

.centering{
    display: flex;
    justify-content: center;
    margin: 40px 0;
}

.temp{
    background-color: rgba(0, 0, 0, 0.411);
    padding: 25px 35px;
    border-radius: 30px;
    color: rgb(51, 51, 51);
    color: antiquewhite;
}

.forecast{
    text-align: center;
    color: antiquewhite;
}

footer{
    position: absolute;
    bottom: 0;
    color: whitesmoke;
    font-size: 8px;
}
